.phone-swiper {
  width: 100%;
  padding: 80px 0;
  background: #b9f8ec;
  border-radius: 30px;

  @media (min-width: $desktop) {
    overflow: hidden;
    background: rgba(185, 248, 236, 0.3);

    &--violet {
      background-color: rgba(232, 213, 252, 0.3);
    }

    &--pistachios {
      background-color: rgba(185, 248, 236, 0.3);
    }

    &--sky {
      background-color: rgba(216, 235, 253, 0.3);
    }
  }
}

.phone-swiper__wrapper {}

.phone-swiper__slide {
  overflow: hidden;
  transition: all .7s;
  background-color: #ffffff;
  border-radius: 20px;
  box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.1);
}

.phone-swiper__slide.swiper-slide-active {
  opacity: 1;
  z-index: 1;
  transform: translateY(-4px) scale(1.1);
  box-shadow: 5px 10px 13px rgba(0, 0, 0, 0.3);
  margin: 0 !important;
}

.phone-swiper__slide.swiper-slide-prev {
  margin-right: -4px !important;
  margin-left: 25px !important;
}

.phone-swiper__slide.swiper-slide-next {
  margin-left: -4px !important;
  margin-right: 25px !important;
}

.phone-swiper__img {
  width: 100%;
  opacity: 0.4;
}

.phone-swiper__slide.swiper-slide-active .phone-swiper__img {
  opacity: 1;
}